.recipe{
    border-radius:10px;
    box-shadow: 0px 5px 20px rgb(73, 73, 73);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    max-width: 45%;
}

.recipe:hover{
   transform: scale(1.01,1.01);
}